.account {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: 1px solid black;
  background-color: #fff;
  width: 80%;
  margin: 0 auto;
  padding: 1.5rem;
  box-sizing: border-box;
  text-align: left;
  margin-bottom: 2rem;
  list-style-type: none;
  &-title {
    margin: 0;
    padding: 0;
    font-size: 1rem;
    font-weight: normal;
  }
  &-amount {
    margin: 0;
    font-size: 2.5rem;
    font-weight: bold;
  }
  &-content-wrapper {
    width: 100%;
    flex: 1;
  }
  .cta {
    flex: 0;
  }
}
.transaction-button {
  display: block;
  width: 200px;
  padding: 8px;
  font-size: 1.1rem;
  font-weight: bold;
  margin-top: 1rem;
  border-color: #00bc77;
  background-color: #005d3b;
  color: #fff;
}
@media all and (max-width: 720px) {
  .account {
    flex-direction: column;
  }

  .account-content-wrapper.cta {
    flex: 1;
  }

  .transaction-button {
    width: 100%;
  }
}
