.editUser {
  width: 400px;
  margin: auto;
}
.editUser h2 {
  color: white;
  margin: 22px;
}
.editUser form {
  margin-bottom: 22px;
}
.editUser fieldset {
  border: none;
  margin-bottom: 10px;
}
.editUser label {
  display: inline-block;
  color: white;
  width: 90px;
}
.editUser input {
  display: inline-block;
  border: 1px solid rgb(204, 204, 204);
  background-color: rgb(204, 204, 204);
  border-radius: 3px;
  height: 27px;
  width: 135px;
  margin-left: 5px;
}
.editUser input:focus {
  border: 1px solid rgb(204, 204, 204);
}
.editUser .userName {
  background: white;
}
.editUser button {
  border: none;
  background-color: #005d3b;
  color: white;
  display: inline-block;
  width: 130px;
  height: 30px;
  border-radius: 3px;
  margin: 0 5px 20px 0;
}/*# sourceMappingURL=EditUser.css.map */