.feature {
  display: flex;
}
.feature-item {
  flex: 1;
  padding: 2.5rem;
}
.feature-item-icon {
  width: 100px;
  border: 10px solid #00bc77;
  border-radius: 50%;
  padding: 1rem;
}
.feature-item-title {
  color: #222;
  font-size: 1.25rem;
  font-weight: bold;
  margin-bottom: 0.5rem;
}

@media all and (max-width: 920px) {
  .feature {
    flex-direction: column;
  }
}/*# sourceMappingURL=Features.css.map */